.App {
  text-align: center;
  background-color: hsl(220, 13%, 18%);
  min-height: 100vh;
  padding: 1rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
 }

.Key-Text {
  word-wrap: break-word;
  white-space: initial;
  width: 100%;
  margin: 1rem;
  color: white;


}
